












































import Vue from 'vue';
import Time from '@/components/Money/Time.vue';
import Notes from '@/components/Money/Notes.vue';
import {Component} from 'vue-property-decorator';
import {RecordItem, RootState} from '@/custom';
import moment, {Moment} from 'moment';

@Component({
  components: {Notes, Time}
})
export default class RecordEdit extends Vue {
  moment = moment;
  id = parseInt(this.$route.params.id);
  record = (this.$store.state as RootState).recordList.filter((item:RecordItem) => item.id === this.id)[0];

  beforeCreate(){
    this.$store.commit('fetchRecords');
  }

  get recordType(){
    if(this.record.type === '-'){
      return '支出';
    }else{
      return '收入';
    }
  }

  goBack(){
    this.$router.replace('/detail');
  }

  remove(record: RecordItem){
    record = this.record;
    this.$store.commit('removeRecord', record);
    this.$router.replace('/detail');
  }
  getCurrentDate(){
    const date = this.record.date;
    this.$emit('update:value', date);
    return date;
  }

  changeDate(moment: Moment) {
    const date = moment.format('YYYY-MM-DD HH:mm');
    this.record.date = date;
    this.$emit('update:value', date);
  }
  save(){
    this.$store.commit('updateRecord', this.record);
    this.$router.replace('/detail');
  }
}
